// src/pages/RealtimeTest.tsx
"use client";
import React, { useEffect, useRef, useState } from "react";
import { supabase } from "../../database/supabaseClient";

export default function RealtimeTest() {
  const [user, setUser] = useState<any>(null);
  const [log, setLog] = useState<string[]>([]);
  const logRef = useRef<HTMLPreElement | null>(null);

  const append = (msg: string) => {
    setLog((prev) => [...prev, `[${new Date().toLocaleTimeString()}] ${msg}`]);
  };

  // Auto-scroll to bottom on new log entries
  useEffect(() => {
    if (logRef.current) {
      logRef.current.scrollTop = logRef.current.scrollHeight;
    }
  }, [log]);

  // Subscribe to realtime messages
  useEffect(() => {
    append("üëÇ Setting up realtime listener...");

    supabase.auth.getUser().then(({ data }) => {
      const currentUser = data?.user ?? null;
      setUser(currentUser);
      if (currentUser) {
        append(`‚úÖ Logged in as ${currentUser.email} (${currentUser.id})`);
      } else {
        append("‚ùå No user logged in");
      }
    });

    const channel = supabase
      .channel("public:messages")
      .on(
        "postgres_changes",
        { event: "INSERT", schema: "public", table: "messages" },
        (payload) => {
          try {
            append(`üí¨ New message event: ${payload.new.event_type}`);
            append(`üì¶ Payload: ${JSON.stringify(payload.new.payload, null, 2)}`);
          } catch (err) {
            append("‚ö†Ô∏è Received event, but failed to format payload.");
          }
        }
      )
      .subscribe((status) => append(`üì° Channel status: ${status}`));

    return () => {
      append("üßπ Unsubscribing...");
      supabase.removeChannel(channel);
    };
  }, []);

  // Send a message row (triggers realtime)
  const sendTestMessage = async () => {
    if (!user) {
      append("‚ùå Cannot send: no logged-in user.");
      return;
    }

    append("üöÄ Inserting new test message via Supabase...");
    const { error } = await supabase.from("messages").insert([
      {
        user_id: user.id,
        event_type: "test_realtime_message",
        payload: {
          from: user.email,
          random_value: Math.floor(Math.random() * 10000),
          note: "Hello from RealtimeTest UI!",
        },
      },
    ]);

    if (error) {
      append(`‚ùå Insert failed: ${error.message}`);
    } else {
      append("‚úÖ Insert request sent. Waiting for realtime callback...");
    }
  };

  return (
    <div
      style={{
        fontFamily: "monospace",
        padding: 20,
        maxWidth: 900,
        margin: "0 auto",
        color: "white",
      }}
    >
      <h2>‚ö° Realtime Test Console</h2>

      {user ? (
        <p>
          ‚úÖ Logged in as:{" "}
          <span style={{ color: "#0f0" }}>
            {user.email || "unknown"} ({user.id})
          </span>
        </p>
      ) : (
        <p>‚ùå Not logged in</p>
      )}

      <div style={{ display: "flex", gap: "10px", marginBottom: "10px" }}>
        <button
          onClick={sendTestMessage}
          style={{
            padding: "8px 14px",
            background: "#0f0",
            color: "#000",
            border: "none",
            borderRadius: 6,
            cursor: "pointer",
            fontWeight: "bold",
          }}
        >
          üì® Send Test Message
        </button>
      </div>

      <pre
        ref={logRef}
        style={{
          background: "#000",
          color: "#0f0",
          padding: 15,
          borderRadius: 8,
          marginTop: 20,
          height: 500,
          fontSize: 12,
          overflowY: "auto",
          whiteSpace: "pre-wrap",
        }}
      >
        {log.join("\n")}
      </pre>
    </div>
  );
}
